import React, { Component } from "react";
import { Router, Switch, Route } from "react-router-dom";



import history from './history';
import Home2 from './Components2/home.jsx'
import Additem from './Components2/additem.jsx'
import Postadd from './Components2/postadd.jsx'
import Sucessfull from './Components2/postsucessfully.jsx'
import Preview from './Components2/previewadd.jsx'



export default class Routes extends Component {
    render() {
        return (
            <Router history={history}>
                <Switch>
                    <Route path="/" exact component={Home2} />
                    <Route path="/Additem" component={Additem} />
                    <Route path="/postadd" component={Postadd} />
                    <Route path="/Sucessfull" component={Sucessfull}/>
                    <Route path="/preview" component={Preview}/>
                   

                    {/* <Route path="/Products" component={Products} /> */}
                </Switch>
            </Router>
        )
    }
}
