

import React from 'react';

import {Header} from '../Components2/Header.jsx'
import Afterheader from '../Components2/afterheader.jsx'
import Afterheaderimage from '../images/AfterHeaderimage.jpg'
import Footer from '../Components2/Footer.jsx'
import Demoimage from '../images/Demopic.jpg'
import addimage from '../images/additem.png'
import history from './../history';
import {firebase} from '../config/firebase'



class Home extends React.Component{


remove=(key)=>{

  var message="If you want to delet enter confirm"
  var con = prompt(message); 

      if (con.toLowerCase() === "confirm" )
    {
    alert("Your Add is Deleted");

    firebase.database().ref('Data').child(key).remove()
    history.push('/')
  

    }
    else
    {
    alert("Your Add is not delete "); 
    } 



  




}

render(){

  const { data } = this.props.location


  
  //console.log(data.data)
  
  return(
    <div>

    <Header/>
    <Afterheader/>


    <div className="Afterheaderimage">
    <center>
    <img src={Afterheaderimage} alt="Usman" />
    </center>
    </div>
  
  
  {/* middle data here */}
  <br/>

<div>
    




<div className="container">
  <div className="row">
    <div className="col pcol">
     <img className="demoimage" src={data.data.profileImg}   alt="" srcset=""/>
    </div>
    <div className="col">
      <div className="pcol2">
          <br/>
        <p className="pheading">{data.data.Price}</p>
        <p className="ptext">{data.data.Tittle}</p>
        
        <p className="ptext">{data.data.Neighbourhood},{data.data.City},{data.data.State}</p>
        
      </div>
<br/>
      <div className="pcol2">


      <img src={addimage} width="60"  alt=""/>
    <p className="ptext2">
    {data.data.Username}
    </p>
    <br/>  
       <br/>
  
        <button onClick={()=>this.remove(data.data.key)} className="pbutton">
        remove
    </button>
    <br/>
    <br/>
      </div>
    </div>

    
  </div>

</div>




<br/>

<div className="container pleft">
  <div className="row">
    <div className="col pcol3">

        <p        className="pheading">
            Detail
        </p>
        <div className="container">
        <div className="row">
          <div className="col ptext">Make</div>
          <div className="col ptext">{data.data.Type}</div>
          <div className="col ptext">Condition</div>
          <div className="col ptext">{data.data.Condition}</div>
        </div>
      </div>
      <br/>
      <hr/>

      <p  className="pheading" >
          Description
      </p>
      <p className="ptext">
      {data.data.Description}
      </p>
    </div>
  
    
  </div>

</div>




















</div>

  <br/>
  
    <div className="Afterheaderimage">
    <center>
    <img src={Afterheaderimage} alt="Usman" />
    </center>
    </div>
  
  
  <Footer/>
  
        </div>
  
  
  
  
  )
}


}





export default Home;

