  import React from 'react';
import history from './../history';
import Footer from '../Components2/Footer.jsx'
import addimage from '../images/additem.png'
import {firebase} from '../config/firebase'

import {storage} from '../config/firebase'


class ADDITEM extends React.Component{
constructor(){
  super()
  this.state={
    profileImg: 'https://icons-for-free.com/iconfiles/png/512/box+document+outline+share+top+upload+icon-1320195323221671611.png',
    Tittle : "",
    Description : "",
    Price : "",
    Username : "",
    MyNumber : "",
    Condition : "New",
    Type : "Samsung",
    State : "Sindh",
    City : "Karachi",
    Neighbourhood : "FB Area",
    Phonenoshow :"on",
    key : firebase.database().ref('Data').push().key

  }
  
}
imagehandler=(e)=>{
  let reader=new FileReader();
  reader.onload=()=>{
    if(reader.readyState ===2 ){
      this.setState({profileImg:reader.result})
    }
  }
  reader.readAsDataURL(e.target.files[0])
  let image=e.target.files[0];
  
  console.log(image)
  const uplaodTask= storage.ref(image.name).put(image);

  uplaodTask.on(
    "state_changed",
    snapshort =>{},
    error =>{
      console.log(error);
    },
    ()=>{
      storage
      .ref()
      .child(image.name)
      .getDownloadURL()
      .then(
        url =>{
          console.log(url);
          this.setState({
            profileImg : url
          })
        }
      );
    }
  )
}   



imagedelete=()=>{

  this.setState({

    profileImg: 'https://icons-for-free.com/iconfiles/png/512/box+document+outline+share+top+upload+icon-1320195323221671611.png'

  })

}



Submit=()=>{


if(this.state.Tittle===""){
  alert('Please Fill the Tittle Field');
}
else if(this.state.Description===""){
  alert('Please Fill the Description Field');
}
else if(this.state.Price===""){
  alert('Please Fill the Price Field');
}
else if(this.state.MyNumber===""){
  alert('Please Fill the Number Field');
}
else if(this.state.Username===""){
  alert('Please Fill the Username Field');
}

else{



  firebase.database().ref('Data').child(this.state.key).push(this.state)

console.log(this.state.key)

  this.props.history.push({
    pathname:'/Sucessfull',
    data:this.state
  })
}


}

 





    render(){

      const {profileImg,Tittle,Description,Price,Username,MyNumber,Condition,Type,State,City,Neighbourhood}=this.state;
  
      return(

<div>

<nav className="navbar navbar-expand-lg navbar-light bg-light">
          
           <a   className="navbar-brand"   onClick={() => history.push('/Additem') }><b>BACK</b></a>
        
          <a   className="navbar-brand" ><b>OLX</b></a>
         
    
        </nav>
      
      
<div>
  <div className="additemcontent">
  <h3>
  POST YOUR AD
    </h3>
    
  </div>
  
  <center>
  <div className="additem">
    <h6>
    SELECTED CATEGORY
    </h6>
<p >
    Mobile/Mobiles       <a onClick={() => history.push('/Additem') } ><b><u>change</u></b></a>
</p>

<hr className="hrline"/>
        
<h6>
  Include Some Detail
    </h6>
    <form action="">

<div>
    <p>Condition *</p>
    <div>
   
    <div className="btn-group btn-group-toggle "   data-toggle="buttons">
        <label className="btn btn-secondary tbutton">
          <input type="radio" name="options" id="option1"   checked /> New
        </label>
        <label className="btn btn-secondary tbutton">
          <input type="radio" name="options" id="option2"   onClick={(e)=>this.setState({Condition : "Used"})} /> Used
        </label>
       
      </div>

    </div>


<br/>
    <p>Type *</p>
    <div>
   
    <div className="btn-group btn-group-toggle "  data-toggle="buttons">
        <label className="btn btn-secondary tbutton">
          <input type="radio" name="options" id="option1" checked  /> Samsung
        </label>
        <label className="btn btn-secondary tbutton">
          <input type="radio" name="options" id="option2"  onClick={(e)=>this.setState({Type : "Dell"})} /> Dell
        </label>
        <label className="btn btn-secondary tbutton">
          <input type="radio" name="options" id="option2" onClick={(e)=>this.setState({Type : "Hp"})} /> Hp
        </label>
        <label className="btn btn-secondary tbutton">
          <input type="radio" name="options" id="option2" onClick={(e)=>this.setState({Type : "Apple"})} /> Apple
        </label>
        <label className="btn btn-secondary tbutton">
          <input type="radio" name="options" id="option2" onClick={(e)=>this.setState({Type : "Others"})} /> Others
        </label>
        
      </div>
<br/>


    </div>


<div>
    
<br/>


<div className="form-group textareapostadd">
        <label htmlFor="exampleFormControlTextarea1">Add Tittle</label>
        <textarea className="form-control"   rows={2}  value={Tittle}  onChange={(e)=>this.setState({Tittle : e.target.value})} />
      
      </div>
</div>



<div>
    
<br/>


<div className="form-group textareapostadd">
        <label htmlFor="exampleFormControlTextarea1">Description</label>
        <textarea className="form-control"  rows={4}   value={Description}  onChange={(e)=>this.setState({Description : e.target.value})} />
      </div>
</div>

<div>


<hr className="hrline"/>
        
        <h5>SET PRICE</h5>
        <p>Price*</p>
        <div className="input-group mb-3 textareapostadd">
        <div className="input-group-prepend">
          <span className="input-group-text">RS</span>
        </div>
        <input type="text" className="form-control" value={Price}  onChange={(e)=>this.setState({Price : e.target.value})}   aria-label="Amount" />
        <div className="input-group-append">
          <span className="input-group-text">.00</span>
        </div>
      </div>        
        <hr className="hrline"/>




</div>


<div>
    <h5>UPLOAD UP TO 12 PHOTO </h5>

    <div className="card imgcard" style={{width: '18rem'}}>
        <img src={profileImg}  className="card-img-top addimg" alt="add" />
        <div className="card-body">
          <input  type="file" style={{display:"none"}} id="customFile" accept="image/*" onChange={this.imagehandler}/>
          <label  className="btn btn-primary upbtn" htmlFor="customFile">&#8593;</label>
          
          <button href="#" className="btn btn-primary btnmargin" onClick={this.imagedelete}>X</button>
       
        </div>
      </div>


</div>


<div>
<hr className="hrline"/>

<h5>CONFIRM YOUR LOCATION </h5>

<div className="form-group textareapostadd">
        <label htmlFor="exampleFormControlSelect1" onChange={(e)=>this.setState({State : e.target.value})} >State*</label>
        <select className="form-control" id="exampleFormControlSelect1">
          <option>Sindh</option>
          <option>Panjab</option>
  
        </select>
      </div>



      <div className="form-group textareapostadd">
        <label htmlFor="exampleFormControlSelect1" onChange={(e)=>this.setState({City: e.target.value})} >City*</label>
        <select className="form-control" id="exampleFormControlSelect1">
          <option>Karachi</option>
          <option>Lahore</option>
  
        </select>
      </div>



      <div className="form-group textareapostadd">
        <label htmlFor="exampleFormControlSelect1">Neighbourhood*</label>
        <select className="form-control" id="exampleFormControlSelect1" onChange={(e)=>this.setState({Neighbourhood : e.target.value})} >
          <option>FB Area</option>
          <option>Model Town</option>
  
        </select>
      </div>

</div>

<div>

<hr className="hrline"/>

<h5>REVIEW YOUR DETAIL </h5>
<div>

<img src={addimage} width="60"  alt=""/>
<input className="form-control form-control-lg textareapostadd2" value={Username}  onChange={(e)=>this.setState({Username : e.target.value})}  type="text" placeholder="Name" />
</div>
<br/>
<h6>Let's verify your account</h6>
<p>We will send you a confirmation code by sms on the next step.</p>

<br/>
<p>Mobile Phone Number *</p>
        <div className="input-group mb-3 textareapostadd">
        <div className="input-group-prepend">
          <span className="input-group-text">+92</span>
        </div>
        <input type="number" requied="requied" className="form-control" value={MyNumber}  onChange={(e)=>this.setState({MyNumber : e.target.value})}  aria-label="Amount" />
        
      </div>   
     <br/>
      <p>Show my phone number on my add</p>     
      <div className="btn-group btn-group-toggle "  data-toggle="buttons">
        <label className="btn btn-secondary tbutton">
          <input type="radio" name="options" id="option1"  checked  /> on
        </label>
        <label className="btn btn-secondary tbutton">
          <input type="radio"  name="options" id="option2"  onClick={(e)=>this.setState({Phonenoshow : "Off"})} /> off
        </label>
       
      </div>
<br/>
</div>


<div>
<hr className="hrline"/>

<br/>
<button type="submit"  onClick={this.Submit} class="btn btn-primary">Post Now</button>
<br/>
</div>







</div>
</form>


<br/>

  </div>
  </center>
  
  
  
  </div>      


<Footer/>
</div>

        )
    }
  
  }
  
  
  
  export default ADDITEM;
  
  